  color: #c1c1c1;
} */

.feature-panel-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  padding: 13px 0;
  text-align: center;
  color: #000;
  background: rgba(255,255,255,.65);
  border: 1px solid #fff;
  box-sizing: border-box;
}

.feature-panel-overlay:hover{
  background: rgba(255,255,255,.95);
}

.feature-panel-overlay h3 {
  margin: 0;
}

h3 {
  font-size: 16px;
}


.cover-me {
  background-size: cover;
  background-repeat: no-repeat;
  display: block;
  position: relative;
}

.widget-title {
  color: #000;
  background: #E6E6FA;
  font: 10px Montserrat, arial, sans-serif;
  letter-spacing: 3px;
  margin: 15px 0px 15px;
  padding: 6px 0px;
  text-align: center;
  text-transform: uppercase;
}

.site-credit{
  text-align: center;
}

#row-footer {
  margin-left:  -15px;
  margin-right: -15px;
}

#main-image {
  max-width: 97%;
}


@media(max-width: 496px) {
  #menu-main-menu{
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 421px) {
  .site-info {
    text-align: center;
  }
}

@media (max-width: 1155px) {
  #social_sidebar {
    display: none;
  }
}

@media(max-width: 768px) {
  #row-footer {
    flex-direction: column;
    align-items: center;
    display: flex;
  }
  .col-sm-4 {
    padding-top: 15px;
  }
}

@media (min-width: 768px)  {
  .col-sm-8 {
    width: 66.6%;
  }

  .col-sm-6{
    width: 50%;
  }

  .col-sm-push-6{
    left: 50%
  }

  .col-sm-pull-6{
    right: 50%
  }

  .col-sm-1m, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11 {
    float: left;
  }

  .col-sm-4 {
    width: 33.3%;
  }
}
